<template>
  <div>
    <el-form label-position="top" label-width="80px" :rules="rules" ref="profileForm" :model="form"
      class="flex flex-wrap">
      <el-form-item label="" prop="institution" class="w-2/4 pl-4">
        <el-input v-model="form.institution" placeholder="" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="caseDataTime" class="w-2/4 pl-4">
        <el-input v-model="form.caseDataTime" placeholder="20141-20226" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="targetTherapy" class="w-2/4 pl-4">
        <el-input v-model="form.targetTherapy" placeholder="(CSP)" maxlength="100" @change="changeTargetTherapy" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="caseNumber" class="w-2/4 pl-4">
        <el-input v-model="form.caseNumber" placeholder="120" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="treatmentMethod" class="w-2/4 pl-4">
        <el-input v-model="form.treatmentMethod" placeholder="(HSP)" maxlength="100" :disabled="!checked" @change="changeTreatmentMethod"></el-input>
      </el-form-item>
      <el-form-item label="" prop="experimentalGroupSize" class="w-2/4 pl-4">
        <el-input v-model="form.experimentalGroupSize" placeholder="110" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="inclusionCriteria" class="w-2/4 pl-4">
        <el-input v-model="form.inclusionCriteria" placeholder="20-65::" :disabled="!checked"
          maxlength="100"></el-input>
      </el-form-item>
      <el-form-item label="" prop="dataCollectionMethod" class="w-2/4 pl-4">
        <el-input v-model="form.dataCollectionMethod" placeholder="" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="ethicalReviewNumber" class="w-2/4 pl-4">
        <el-input v-model="form.ethicalReviewNumber" placeholder="4564364374" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="exclusionCriteria" class="w-2/4 pl-4">
        <el-input v-model="form.exclusionCriteria" placeholder="" :disabled="!checked"
          maxlength="100"></el-input>
      </el-form-item>
      <el-form-item label="" prop="statisticalTool" class="w-2/4 pl-4">
        <el-input v-model="form.statisticalTool" placeholder="R" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="missingProcessingTool" class="w-2/4 pl-4">
        <el-input v-model="form.missingProcessingTool" placeholder="Pythonsklearn" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="missingHandlingMethod" class="w-full pl-4">
        <el-input v-model="form.missingHandlingMethod"
          placeholder=";:" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        institution: "",
        caseDataTime: "",
        targetTherapy: "",
        caseNumber: "",
        treatmentMethod: "",
        experimentalGroupSize: "",
        inclusionCriteria: "20-65::",
        dataCollectionMethod: "",
        ethicalReviewNumber: "",
        exclusionCriteria: "",
        statisticalTool: "R",
        missingProcessingTool: "Pythonsklearn",
        missingHandlingMethod: ";:",
      },
      rules: {
        institution: [{ required: false, message: "", trigger: "blur" }],
        caseDataTime: [{ required: false, message: "", trigger: "blur" }],
        targetTherapy: [{ required: false, message: "", trigger: "blur" }],
        caseNumber: [{ required: false, message: "", trigger: "blur" }],
        treatmentMethod: [{ required: false, message: "", trigger: "blur" }],
        experimentalGroupSize: [{ required: false, message: "", trigger: "blur" }],
        inclusionCriteria: [{ required: false, message: "", trigger: "blur" }],
        dataCollectionMethod: [{ required: false, message: "", trigger: "blur" }],
        ethicalReviewNumber: [{ required: false, message: "", trigger: "blur" }],
        exclusionCriteria: [{ required: false, message: "", trigger: "blur" }],
        statisticalTool: [{ required: false, message: "", trigger: "blur" }],
        missingProcessingTool: [{ required: false, message: "", trigger: "blur" }],
        missingHandlingMethod: [{ required: false, message: "", trigger: "blur" }],
      }
    }
  },
  methods: {
    resetRequiredForm(value) {
      this.rules.institution[0].required = value
      this.rules.caseDataTime[0].required = value
      this.rules.targetTherapy[0].required = value
      this.rules.caseNumber[0].required = value
      this.rules.treatmentMethod[0].required = value
      this.rules.experimentalGroupSize[0].required = value
      this.rules.inclusionCriteria[0].required = value
      this.rules.dataCollectionMethod[0].required = value
      this.rules.ethicalReviewNumber[0].required = value
      this.rules.exclusionCriteria[0].required = value
      this.rules.statisticalTool[0].required = value
      this.rules.missingProcessingTool[0].required = value
      this.rules.missingHandlingMethod[0].required = value
    },
    changeTargetTherapy(value) {
      this.$bus.emit('fromTargetTherapy', value)
    },
    changeTreatmentMethod(value) {
      this.$bus.emit('fromTreatmentMethod', value)
    },
  },
  created() {
    this.$bus.on("fromTargetTherapy", data => {
      this.form.targetTherapy = data
    })
    this.$bus.on("fromTreatmentMethod", data => {
      this.form.treatmentMethod = data
    })
  }
}
</script>
