<template>
  <div>
    <el-form label-position="top" label-width="80px" :rules="rules" ref="introductionForm" :model="form"
      class="flex flex-wrap">
      <el-form-item label="" prop="seriousDisease" class="w-2/4 pl-4">
        <el-input v-model="form.seriousDisease" placeholder="" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="indisposition" class="w-2/4 pl-4">
        <el-input v-model="form.indisposition" placeholder="" maxlength="100" :disabled="!checked"
          @change="changeIndisposition"></el-input>
      </el-form-item>
      <el-form-item label="" prop="illnessName" class="w-2/4 pl-4">
        <el-input v-model="form.illnessName" placeholder="" maxlength="100" :disabled="!checked"
          @change="changeIllnessName"></el-input>
      </el-form-item>
      <el-form-item label="" prop="targetTherapy" class="w-2/4 pl-4">
        <el-input v-model="form.targetTherapy" placeholder="CSP" maxlength="100" :disabled="!checked"
          @change="changeTargetTherapy"></el-input>
      </el-form-item>
      <el-form-item label="" class="w-2/4 pl-4">
        <el-input v-model="form.innovationPoint" placeholder="1  2CSP" maxlength="100" :disabled="!checked"
          @change="changeInnovationPoint"></el-input>
      </el-form-item>
      <el-form-item label="" prop="treatmentMethod" class="w-2/4 pl-4">
        <el-input v-model="form.treatmentMethod" placeholder="HSP" maxlength="100" :disabled="!checked"
          @change="changeTreatmentMethod"></el-input>
      </el-form-item>
      <el-form-item label="" class="w-full pl-4">
        <el-input v-model="form.paperTitle" placeholder="AI" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="organization" class="w-full pl-4">
        <el-input v-model="form.organization" placeholder="" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="knownTreatments" class="w-full pl-4">
        <el-input v-model="form.knownTreatments" placeholder="" :disabled="!checked"
          maxlength="100"></el-input>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    checked: { type: Boolean, default: false },
  },
  data() {
    return {
      form: {
        seriousDisease: "",
        indisposition: "",
        illnessName: "",
        targetTherapy: "",
        innovationPoint: "",
        treatmentMethod: "",
        paperTitle: "",
        organization: "",
        knownTreatments: ""
      },
      rules: {
        seriousDisease: [{ required: false, message: "", trigger: "blur" }],
        indisposition: [{ required: false, message: "", trigger: "blur" }],
        illnessName: [{ required: true, message: "", trigger: "blur" }],
        targetTherapy: [{ required: true, message: "", trigger: "blur" }],
        treatmentMethod: [{ required: true, message: "", trigger: "blur" }],
      },
    }
  },
  methods: {
    resetRequiredForm(value) {
      this.rules.seriousDisease[0].required = value
      this.rules.indisposition[0].required = value
    },
    getPaperTitle() {
      this.form.paperTitle = this.form.treatmentMethod && this.form.illnessName ? this.form.treatmentMethod + "" + this.form.illnessName + "" : ""
    },
    changeIllnessName(value) {
      this.$bus.emit('fromIllnessName', value)
      this.getPaperTitle()
    },
    changeIndisposition(value) {
      this.$bus.emit('fromIndisposition', value)
    },
    changeTargetTherapy(value) {
      this.$bus.emit('fromTargetTherapy', value)
    },
    changeInnovationPoint(value) {
      this.$bus.emit('fromInnovationPoint', value)
    },
    changeTreatmentMethod(value) {
      this.getPaperTitle()
      this.$bus.emit('fromTreatmentMethod', value)
    },
  },
  created() {
    this.$bus.on("fromIndisposition", data => {
      this.form.indisposition = data
    })
    this.$bus.on("fromTargetTherapy", data => {
      this.form.targetTherapy = data
    })
    this.$bus.on("fromTreatmentMethod", data => {
      this.form.treatmentMethod = data
    })
    this.$bus.on("fromIllnessName", data => {
      this.form.illnessName = data
      this.getPaperTitle()
    })
    this.$bus.on("fromInnovationPoint", data => {
      this.form.innovationPoint = data
    })
  }
}
</script>
