<template>
  <div>
    <el-form label-position="top" label-width="80px" :rules="rules" ref="discussForm" :model="form"
      class="flex flex-wrap">
      <el-form-item label="" prop="indisposition" class="w-2/4 pl-4">
        <el-input v-model="form.indisposition" placeholder="" maxlength="100" @change="changeIndisposition" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="illnessName" class="w-2/4 pl-4">
        <el-input v-model="form.illnessName" placeholder="" maxlength="100" @change="changeIllnessName" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="targetTherapy" class="w-2/4 pl-4">
        <el-input v-model="form.targetTherapy" placeholder="(CSP)" maxlength="100" @change="changeTargetTherapy" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="treatmentMethod" class="w-2/4 pl-4">
        <el-input v-model="form.treatmentMethod" placeholder="(HSP)" maxlength="100" @change="changeTreatmentMethod" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="futureDirection" class="w-full pl-4">
        <el-input v-model="form.futureDirection" placeholder=":" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="innovationPoint" class="w-full pl-4">
        <el-input v-model="form.innovationPoint" placeholder="" maxlength="100" @change="changeInnovationPoint" :disabled="!checked"></el-input>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    checked: { type: Boolean, default: false },
  },
  data() {
    return {
      form: {
        indisposition: "",
        illnessName: "",
        targetTherapy: "",
        treatmentMethod: "",
        futureDirection: ":",
        innovationPoint: "",
      },
      rules: {
        indisposition: [{ required: false, message: "", trigger: "blur" }],
        illnessName: [{ required: false, message: "", trigger: "blur" }],
        targetTherapy: [{ required: false, message: "", trigger: "blur" }],
        treatmentMethod: [{ required: false, message: "", trigger: "blur" }],
        futureDirection: [{ required: false, message: "", trigger: "blur" }],
        innovationPoint: [{ required: false, message: "", trigger: "blur" }],
      },
    }
  },
  methods: {
    resetRequiredForm(value) {
      this.rules.indisposition[0].required = value
      this.rules.illnessName[0].required = value
      this.rules.targetTherapy[0].required = value
      this.rules.treatmentMethod[0].required = value
      this.rules.futureDirection[0].required = value
    },
    changeIndisposition(value) {
      this.$bus.emit('fromIndisposition', value)
    },
    changeTargetTherapy(value) {
      this.$bus.emit('fromTargetTherapy', value)
    },
    changeTreatmentMethod(value) {
      this.$bus.emit('fromTreatmentMethod', value)
    },
    changeIllnessName(value) {
      this.$bus.emit('fromIllnessName', value)
    },
    changeInnovationPoint(value) {
      this.$bus.emit('fromInnovationPoint', value)
    },
  },
  created() {
    this.$bus.on("fromTargetTherapy", data => {
      this.form.targetTherapy = data
    })
    this.$bus.on("fromTreatmentMethod", data => {
      this.form.treatmentMethod = data
    })
    this.$bus.on("fromInnovationPoint", data => {
      this.form.innovationPoint = data
    })
    this.$bus.on("fromIllnessName", data => {
      this.form.illnessName = data
    })
    this.$bus.on("fromIndisposition", data => {
      this.form.indisposition = data
    })
  }
}
</script>
